.header__top {
  padding-left: 98px;
  padding-right: 98px;
  margin: 0 auto;
  max-width: 1366px;
  display: flex;
  flex-direction: row;
  justify-content: space-between; }

/* Padding сверху и снизу от header__search и header__login
ставлю на 6 px меньше чем в макете, потому что там мерят по блоку высотой 10px, а на практике это 21px */
.header__search {
  width: 169px;
  padding-left: 29px;
  padding-top: 41px;
  padding-bottom: 52px;
  position: relative; }

.header__search::before {
  content: "";
  width: 14px;
  height: 14px;
  background-image: url("../../img/icon-search.svg");
  position: absolute;
  left: 0px;
  top: 45px; }

.header__search input {
  border: none;
  font-size: 14px;
  line-height: 21px;
  font-weight: 500; }

.header__search input::placeholder {
  color: #9B9997; }

.header__logo {
  padding-top: 30px; }

.header__login {
  width: 169px;
  text-align: right;
  padding-top: 41px;
  padding-bottom: 52px; }

.header__login span {
  margin-left: 5px; }

.header__login a {
  margin-right: 30px;
  border: none;
  background: none; }

.header__login a:hover, .header__login span:hover {
  color: #9B9997; }

.header__bottom {
  border-top: 1px solid #F6F5F4; }

.page {
  min-width: 320px;
  font-family: "Poppins", Arial, sans-serif;
  color: #1D1613;
  font-weight: 500; }

.nav__list {
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  max-width: 1366px;
  justify-content: center;
  padding-top: 18px;
  padding-bottom: 19px; }

.nav__item {
  list-style: none;
  margin-right: 35px;
  font-size: 16px;
  line-height: 24px;
  font-weight: 500px; }

.nav__item:hover {
  color: #9B9997; }

.intro {
  padding-top: 195px;
  padding-bottom: 194px;
  text-align: center;
  background-image: url("../../img/intro.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  position: relative; }
  @media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
    .intro {
      background-image: url("../../img/intro@2x.jpg"); } }

.intro::before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  background-color: rgba(86, 64, 52, 0.7);
  position: absolute;
  top: 0;
  z-index: 1; }

.intro h2 {
  text-transform: uppercase;
  font-family: "Cormorant Garamond", Arial, sans-serif;
  color: #FFFFFF;
  font-size: 38px;
  line-height: 49px;
  letter-spacing: 0.03em;
  margin: 0;
  position: relative;
  z-index: 2; }

.intro p {
  margin-bottom: 0;
  margin-top: 20px;
  font-style: italic;
  font-family: "Cormorant Garamond", Arial, sans-serif;
  color: #FFFFFF;
  font-size: 38px;
  line-height: 49px;
  position: relative;
  z-index: 2; }

.new {
  padding-top: 125px;
  max-width: 1170px;
  margin: 0 auto;
  position: relative; }

.new__title {
  margin-top: 0;
  text-align: center;
  font-family: "Cormorant Garamond", Arial, Helvetica, sans-serif;
  font-size: 34px;
  line-height: 44px;
  margin-bottom: 0; }

.new__span {
  display: block;
  margin: 0 auto;
  width: 87px;
  font-size: 16px;
  line-height: 22px;
  text-align: center;
  position: absolute;
  top: 187px;
  left: 50%;
  transform: translateX(-50%); }

.new__span::before {
  content: "";
  display: block;
  width: 80px;
  height: 1px;
  background-color: #A87B62;
  position: absolute;
  bottom: 1px;
  left: 3px; }

.new__list {
  list-style: none;
  margin: 107px auto;
  margin-bottom: 55px;
  max-width: 1170px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-left: 0; }

.card {
  width: max-content;
  max-width: 270px; }

.card p {
  margin: 0;
  text-align: center;
  padding-top: 25px;
  padding-bottom: 12px; }

.card span {
  display: block;
  font-size: 14px;
  line-height: 18px;
  font-weight: 500;
  text-align: center; }

.blog {
  padding-top: 125px; }

.blog__title {
  margin-top: 0;
  text-align: center;
  font-family: "Cormorant Garamond", Arial, Helvetica, sans-serif;
  font-size: 34px;
  line-height: 44px;
  margin-bottom: 30; }

.blog p {
  display: block;
  margin: 0 auto;
  margin-bottom: 60px;
  width: fit-content;
  font-size: 16px;
  line-height: 22px;
  text-align: center;
  position: relative; }

.blog p::before {
  content: "";
  display: block;
  width: 64px;
  height: 1px;
  background-color: #A87B62;
  position: absolute;
  bottom: 1px;
  left: 0px; }

.blog__list {
  display: grid;
  padding: 0;
  grid-template-columns: 1fr 1fr;
  grid-gap: 30px;
  max-width: 1170px;
  margin: 0 auto;
  list-style: none; }

.article {
  display: flex;
  flex-direction: row; }

.article--big {
  position: relative;
  grid-column: 2/3;
  grid-row: 1/3; }

.article__article {
  text-align: center;
  flex-grow: 2;
  background-color: #F0EAE4; }

.article__article--power {
  order: -2; }

.article__img-wrapper {
  width: 100%; }

.article__article--big {
  position: absolute;
  width: 285px;
  height: 300px;
  bottom: 0px;
  right: 0px; }

.article__article span {
  color: #9B9997;
  font-size: 14px;
  line-height: 18px;
  margin-top: 70px;
  display: block; }

.article__article h3 {
  font-family: "Cormorant Garamond", Arial, sans-serif;
  font-weight: 600;
  font-size: 26px;
  line-height: 34px; }

.article__article a {
  font-size: 16px;
  display: block;
  width: fit-content;
  margin: 0 auto;
  position: relative; }

.article__article a::before {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  background-color: #A87B62;
  position: absolute;
  bottom: 1px;
  left: 0px; }
